
import './styles.css';

import { useState, useEffect } from 'react';
import PlayerListItem from './PlayerListItem';
import PlayersFilter from './PlayersFilter';

import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import ReactCountryFlag from "react-country-flag"
import { FaArrowUp, FaArrowDown } from "react-icons/fa";

const PlayerList = ({ vctPlayers, onSelectPilot, showForMobile, showHiddenPlayers }) => {

    const [ scrollThumb, setScrollThumb ] = useState("tw-scrollbar-thumb-blue-700");
    const [ scrollTrack, setScrollTrack ] = useState("tw-scrollbar-track-blue-300");

    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    const [ sorting, setSorting ] = useState("Name");
    const [ sortingDirection, setSortingDirection ] = useState("ascending");

    const [platformFilter, setPlatformFilter] = useState('all');
    const [vctCountries, setVctCountries] = useState([]);
    const [countryFilter, setCountryFilter] = useState('all');

    const [countLegacyPilots, setCountLegacyPilots] = useState(true);
    const [filteredPlayers, setFilteredPlayers] = useState(vctPlayers);

    useEffect(()=>{
        let theme = JSON.parse(localStorage.getItem('theme'));

        if ( !theme || theme === '7' ) {
            setScrollThumb( "tw-scrollbar-thumb-blue-700" );
            setScrollTrack( "tw-scrollbar-track-blue-300" );
            
        } else if ( theme === 'Z' ) {
            setScrollThumb( "tw-scrollbar-thumb-red-700" );
            setScrollTrack( "tw-scrollbar-track-red-300" );

        } else if ( theme === '5' ) {
            setScrollThumb( "tw-scrollbar-thumb-green-700" );
            setScrollTrack( "tw-scrollbar-track-green-300" );
        }

        countCountries( platformFilter, countLegacyPilots );

        if ( vctPlayers ) {
            if ( vctPlayers.length > 0 ) {
                filterPlayers( platformFilter, countryFilter, sorting, sortingDirection, countLegacyPilots );
            }
        }
    },[vctPlayers]);

    const countCountries = ( plat, countLegacy ) => {
        let metaVct = [], newPlayers=[], alphaPlayers=[];

        if ( countLegacy ) {
            alphaPlayers = vctPlayers.slice();
        } else {
            for (const player of vctPlayers) {
                if ( player.active ) {
                    alphaPlayers.push( player ); 
                }
            }
        }

        if ( plat === 'all' ) {
            newPlayers = alphaPlayers.slice()
        } else {
            for (const player of alphaPlayers) {
                if ( player.platforms.indexOf( plat ) > -1 ) {
                    newPlayers.push( player ); 
                }
            }
        }

        newPlayers.forEach(player => {
            let found = false;

            for (let i = 0; i < metaVct.length; i++) {
                const country = metaVct[i];
                
                if ( country.code === player.country.code ) {
                    country.cant = country.cant + 1;
                    found = true;
                    break;
                }
            }

            if ( !found ) {
                metaVct.push({ 
                    code:player.country.code, cant:1 
                });
            }
        });

        metaVct.sort(function (a, b) {
            return (a.cant > b.cant) ? -1 : (a.cant < b.cant) ? 1 : 0;
        });

        setVctCountries( metaVct );
    }

    const selectSortingMethod = ( sort ) => {
        setSorting( sort );
        playSelect();
        filterPlayers( platformFilter, countryFilter, sort, sortingDirection, countLegacyPilots );
    }

    const selectSortingDirection = ( dir ) => {
        setSortingDirection( dir );
        playSelect();
        filterPlayers( platformFilter, countryFilter, sorting, dir, countLegacyPilots );
    }

    const selectPlatformFilter = ( filter ) => {
        setPlatformFilter( filter );
        countCountries( filter, countLegacyPilots );
        filterPlayers( filter, countryFilter, sorting, sortingDirection, countLegacyPilots );
    }

    const selectCountryFilter = ( pais ) => {
        setCountryFilter( pais );
        playSelect();
        filterPlayers( platformFilter, pais, sorting, sortingDirection, countLegacyPilots );
    }

    const setLegacyPilots = ( lg ) => {
        setCountLegacyPilots( lg ) 
        playSelect();
        countCountries( platformFilter, lg );
        filterPlayers( platformFilter, countryFilter, sorting, sortingDirection, lg );
    }

    const filterPlayers = ( plat, country, sort, dir, includeLegacy ) => {
        if ( vctPlayers ) {
            let alphaPlayers = [];
            let newPlayers = [];

            if ( includeLegacy ) {
                alphaPlayers = vctPlayers.slice();
            } else {
                for (const player of vctPlayers) {
                    if ( player.active ) {
                        alphaPlayers.push( player ); 
                    }
                }
            }

            if ( plat === 'all' ) {
                newPlayers = alphaPlayers.slice()
            } else {
                for (const player of alphaPlayers) {
                    if ( player.platforms.indexOf( plat ) > -1 ) {
                        newPlayers.push( player ); 
                    }
                }
            }

            let secondFilterVct = [];

            if ( country === 'all' ) {
                secondFilterVct = newPlayers.slice();
            } else {
                for (const player of newPlayers) {
                    if ( player.country.code.indexOf( country ) > -1 ) {
                        secondFilterVct.push( player ); 
                    }
                }
            }

            if ( sort === 'Name' ) {

                secondFilterVct.sort(function (a, b) {
                    let nameA = a.name;
                    let nameb = b.name;

                    if ( dir === 'ascending' ) {
                        return (nameA < nameb) ? -1 : (nameA > nameb) ? 1 : 0;
                    } else {
                        return (nameA > nameb) ? -1 : (nameA < nameb) ? 1 : 0;
                    }
                });
            } 
            else if ( sort === 'Rank' ) {
                secondFilterVct.sort(function (a, b) {
                    if ( dir === 'ascending' ) {
                        return (a.rankAverage > b.rankAverage) ? -1 : (a.rankAverage < b.rankAverage) ? 1 : 0;
                    } else {
                        return (a.rankAverage < b.rankAverage) ? -1 : (a.rankAverage > b.rankAverage) ? 1 : 0;
                    }
                });
            }

            let lastVct = [];

            if ( showHiddenPlayers ) {
                lastVct = secondFilterVct.slice();
            } else {
                for (const player of secondFilterVct) {
                    if ( !player.hidden ) {
                        lastVct.push( player ); 
                    }
                }
            }

            setFilteredPlayers( lastVct );
        }
    }

    return (
        <div className="pilot-list-container tw-flex tw-flex-col tw-flex-grow tw-px-4 tw-pt-12 tw-pb-24 tw-h-screen">
            <div>Platform:</div>
            <PlayersFilter selectFilter={ selectPlatformFilter } filter={ platformFilter } />
            
            <div>Country:</div>
            <div className="tw-flex tw-flex-row tw-justify-around tw-py-2 tw-items-center">
                <div onClick={ () => selectCountryFilter('all') } className={`player-list-item-name ${countryFilter === "all" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                    ALL
                </div>
                <div className={`country-filter tw-flex tw-flex-row tw-overflow-visible tw-overflow-x-auto tw-scrollbar-thin ${scrollThumb} ${scrollTrack}`}>
                { vctCountries.map( country => <div className="tw-flex tw-flex-row tw-items-center tw-px-2" onClick={ () => selectCountryFilter(country.code) }>
                    <ReactCountryFlag countryCode={ country.code } style={{ fontSize: "2em" }} aria-label={ country.code }/>
                    <h3 className={`tw-pl-1 ${countryFilter === country.code ? "active-player-list-item" : "inactive-player-list-item"}`}>{country.cant}</h3>
                </div> )}
                </div>
            </div>

            <div>Sort by:</div>
            <div className="tw-flex tw-flex-row tw-justify-around tw-py-2">
                <div onClick={ () => selectSortingMethod('Name') } className={`player-list-item-name ${sorting === "Name" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                    Name
                </div>
                <div onClick={ () => selectSortingMethod('Rank') } className={`player-list-item-name ${sorting === "Rank" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                    Rank
                </div>
                <div onClick={ () => selectSortingDirection('descending') } className={`player-list-item-name ${sortingDirection === "ascending" ? '' : 'tw-hidden'}`} onMouseEnter={ playNavigate } >
                    <FaArrowUp />
                </div>
                <div onClick={ () => selectSortingDirection('ascending') } className={`player-list-item-name ${sortingDirection === "descending" ? '' : 'tw-hidden'}`} onMouseEnter={ playNavigate } >
                    <FaArrowDown />
                </div>
            </div>
            
            <div className={`tw-overflow-visible tw-overflow-y-auto tw-scrollbar-thin ${scrollThumb} ${scrollTrack}`}>
                { filteredPlayers.map( (player, index) => (
                        <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ sorting === 'Rank' ? (sortingDirection === 'ascending' ? index+1 : filteredPlayers.length-index ) : -1 }/>      
                ))}
            </div>

            <div className="tw-flex tw-flex-row tw-justify-around tw-py-2">
                <span>Include Legacy Pilots:</span>
                <span onClick={ () => setLegacyPilots( true ) } className={`player-list-item-name ${countLegacyPilots ? "active-player-list-item" : "inactive-player-list-item"}`}>YES</span>
                <span onClick={ () => setLegacyPilots( false ) } className={`player-list-item-name ${!countLegacyPilots ? "active-player-list-item" : "inactive-player-list-item"}`}>NO</span>
            </div>
        </div>
    )
}

export default PlayerList
